import React from "react";
import { AnimatePresence } from "framer-motion";
import View from "../components/View";
import Navbar from "../components/Navbar";
import { useRouter } from "next/router";
// import { withRedux } from "../helpers/withRedux";

import withRedux from "next-redux-wrapper";
import { initializeStore } from "../store/reducer";
import { Provider } from "react-redux";

import App from "next/app";
import DataContextProvider from "../helpers/dataContext";
import Div100vh from "react-div-100vh";
import { useViewportDimensions } from "../hooks/useViewportDimensions";

class MyApp extends App {
  constructor() {
    super();
  }
  static async getInitalProps({ Component, ctx }) {
    return {
      pageProps: Component.getInitalProps
        ? await Component.getInitalProps(ctx)
        : {}
    };
  }
  render() {
    const { Component, pageProps, store, router } = this.props;
    return (
      <Provider store={store}>
        <Div100vh
          style={{
            position: "absolute",
            inset: "0 0 0 0",
            width: `100vw`,
            height: `100rvh`,
            overflow: `hidden`
          }}
        >
          <DataContextProvider
            innovations={pageProps.innovations}
            humans={pageProps.humans}
            about={pageProps.about}
          >
            <Navbar />
            <AnimatePresence initial={false}>
              <Component {...pageProps} key={router.pathname} />
            </AnimatePresence>
          </DataContextProvider>
        </Div100vh>
      </Provider>
    );
  }
}

export default withRedux(initializeStore)(MyApp);
